.user__settings {
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
}

.user__settings-details {
  width: 25%;
  height: calc(100vh - 100px);
  border-right: 1px solid grey;
  display: flex;
  flex-direction: column;
  position: relative;
}

.user__settings-details-image {
  width: 100%;
  padding: 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.user__settings-details-image img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
}

.user__settings-details-image h3 {
  margin: 20px 0;
}

.user__settings .sectionsOfUser {
  width: 100%;
  margin-top: 30px;
  position: relative;
  flex: 1;
}

.user__settings .sectionsOfUser p {
  width: 90%;
  margin: 2px auto;
  padding: 10px;
  display: flex;
}

.user__settings .sectionsOfUser p a {
  color: white;
  font-size: large;
  font-family: "Josefin Slab";
  padding: 15px;
  background-color: #d70f64;
  font-weight: 600;
  letter-spacing: 1px;
  transition: all 0.5s ease;
  flex: 1;
}

.user__settings .sectionsOfUser p a:hover {
  background-color: #ff006a;
}

.user__settings .sectionsOfUser p a:active {
  background: #f15495;
  transform: translateY(4px);
}

.user__settings-section {
  flex: 1;
  padding: 20px;
}

.user__settings-section .main__details {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.user__settings-section .main__details .name {
  min-width: 280px;
  width: 50%;
  flex: 1 1 45%;
  margin: 0 5px;
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
  background: #ddd;
  padding: 10px;
}

.sectionsOfUser2 {
  display: none;
}

.user__settings-section .main__details .name p {
  font-family: "Josefin Slab";
}
.user__settings-section .main__details .name .title {
  font-size: large;
  font-weight: 700;
  letter-spacing: 0.6px;
}

.user__settings-section .main__details .name .desc {
  font-size: medium;
  font-weight: 600;
  letter-spacing: 0.6px;
}

.user__settings-section .main__details .name p:last-child {
  text-align: right;
}

@media only screen and (max-width: 1300px) {
  .user__settings-details {
    width: 30%;
  }
  .user__settings-section {
    width: 70%;
  }
}

@media only screen and (max-width: 1100px) {
  .user__settings-details {
    width: 35%;
  }
  .user__settings-section {
    width: 65%;
  }
}

@media only screen and (max-width: 900px) {
  .user__settings-details {
    width: 40%;
  }
  .user__settings-section {
    width: 60%;
  }
}

@media only screen and (max-width: 750px) {
  .user__settings-details {
    width: 45%;
  }
  .user__settings-section {
    width: 55%;
  }
}

@media only screen and (max-width: 650px) {
  .user__settings {
    flex-direction: column;
  }
  .user__settings .sectionsOfUser1 {
    display: none;
  }

  .user__settings .sectionsOfUser2 {
    display: block;
  }
  .user__settings-details {
    width: 100%;
    border: none;
    height: fit-content;
    border-right: none;
  }
  .user__settings-section {
    width: calc(100% - 20px);
    padding: 10px;
  }

  .main__details .name {
    min-height: 50px;
  }
}

@media only screen and (max-width: 400px) {
  .user__settings-section {
    width: 100%;
    padding: 0;
  }
}
